@font-face{
  font-family: 'slack';
  font-style: normal;
  src: url(/robofriends/static/media/Slackey-Regular.9a708e71.ttf);
  font-weight: 100;  
};


body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-gradient(linear, left top, right top, from(rgb(159, 152, 96)), color-stop(rgb(48, 207, 155)), to(rgb(15, 84, 111)));
  background: linear-gradient(to right, rgb(159, 152, 96), rgb(48, 207, 155), rgb(15, 84, 111));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1{
  font-family: 'slack';
  font-size: 50px;
  color: rgba(3, 56, 39, 0.8);
  border: 3px solid;
  border-radius: 8px;
  -webkit-border-image: -webkit-gradient(linear, left top, left bottom, from(red), color-stop(1, rgba(0, 0, 0, 0)));
  -webkit-border-image: linear-gradient(to bottom, red, rgba(0, 0, 0, 0) 1 100%);
          border-image: -webkit-gradient(linear, left top, left bottom, from(red), color-stop(1, rgba(0, 0, 0, 0)));
          border-image: linear-gradient(to bottom, red, rgba(0, 0, 0, 0) 1 100%);
  text-shadow: 2px 1px 1px black;
}

.card-img{
  width:230px;
}


.fixed-navbar{
    overflow-y: scroll;
    height: 500px;
}
